@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Atkinson+Hyperlegible:ital,wght@0,400;0,700;1,400;1,700&family=Bricolage+Grotesque:opsz,wght@12..96,200..800&family=Concert+One&family=Instrument+Sans:ital,wght@0,400..700;1,400..700&family=Libre+Franklin:ital,wght@0,100..900;1,100..900&family=Pangolin&family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&family=Reddit+Sans:ital,wght@0,200..900;1,200..900&display=swap');
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Bricolage Grotesque", sans-serif;
    list-style-type: none;
}
body{
    width: 100%;
    height: fit-content;
    
}
.container{
    width: 100%;
    height: 100%;
    /* display: flex; */
    justify-content: center;
}
.wrapper{
    max-width: 1160px;
    width: 100%;
    /* height: 300px; */
    /* background-color: red; */
    margin: auto;
}
.hero-txt{
    width: 100%;
    padding:10px;
}
.large-txt{
    
    font-size: 90px;
    text-align: center;
    margin-top: 60px;
}
header{
    width: 100%;
    height: 65px;
    border-bottom: 1px solid rgba(0,0,0,.1);
    display: flex;
    justify-content: space-between;
    padding: 7.5px 20px;
}

.logo{
    max-width: 200px;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    /* background-color: rgba(0,0,0,.05); */
    display: flex;
    align-items: center;

}
.logo img{
    /* height: 90%; */
    width: 100%;
}
.more-options{
    width:auto;
    border-radius: 5px;
    padding: 5px;
    /* background-color: rgba(0,0,0,.05); */
}
.more-options button{
    background-color: #0b0b0b;
    border-radius: 5px;
    color: #fff;
    height: 100%;
    padding: 10px 0px;
    width: 150px;
    font-weight: bold;
    border: none;
}
p{
    font-family: "Sniglet", system-ui;
    text-align: center;
    font-size: 20px;

}
.custom-filter{
    width: 100%;
    height: fit-content;
}
.header{
    width: 100%;
    height: 75px;
    /* background-color: #f2f2f2; */
    border-bottom: 1px solid rgba(0,0,0,.05);
    display: flex;
    overflow-x: scroll;
    padding: 20px 10px;
    transition: .5s all;
}
.header::-webkit-scrollbar ,.games::-webkit-scrollbar{
    width: 5px;
    background-color: var(--post-color2);
    border-radius: 10px;
    display: none;
}
.filter{
    width: fit-content;
    display: flex;
    height: 100%;
    margin: auto;
}
.filter li{
    height: 100%;
    border: 1px solid #000;
    border-radius: 5px;
    margin: 0px 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 15px;
    background-color: #fff;
    font-weight: bold;
}
.filter li.active{
    background-color: #000;
    color: #fff;
}
@media screen and (max-width:770px) {
    .large-txt{
        font-size: 10vmin;
    }
}
@media screen and (max-width:550px) {
    p{
        font-size: 16px;
    }
}
#show-header{
    position: sticky;
    top:0px;
    z-index: 2;
    backdrop-filter: saturate(180%) blur(5px);
    background: hsla(0, 0%, 100%, .8);
}
.games{
    max-width: 900px;
    margin: auto;
    width: 100%;
    /* height: fit-content; */
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 10px;

}
.games li{
    max-width: 50%;
    width: 45%;
    height: 400px;
    border: 5px solid #fff;
    outline: 1px solid rgba(0,0,0,.1);
    border-radius: 15px;
    margin: 5px;
    z-index: 0;
    /* cursor: pointer; */
    background-color: rgba(0,0,0,.05);
    overflow: hidden;
    transition: .5s;
    transform: scaleX(1) scaleY(1);
}
.games li:active .game-holder{
    transform: scaleX(1.05) scaleY(1.05);
}
footer{
    width: 100%;
    height: auto;
    background-color: #f0f2fa;
    /* position: absolute; */
    bottom: 0px;
    display: flex;
    justify-content: center;
    padding: 10px;
}
.footer-intro {
    max-width: 1160px;
    width: 100%;
    height: auto;
    /* display: flex; */
    /* justify-content: center; */
    padding-top: 10px;
}
.footer-intro .p{
    font-family: "Bricolage Grotesque", sans-serif;
    font-size: smaller;
    font-weight: bold;
}
.footer-intro .p2{
    font-size: smaller;
}
.first{
    width: 50px;
    height: 35px;
    display: flex;
    justify-content: center;
}
.first img{
    height: 100%;

}
.footer-head{
    display: flex;
    height: fit-content;
    align-items: center;
    justify-content: center;
}
.text{
    width: auto;
    padding: 0px 5px;
    display: flex;
    align-items: center;
}
a{
    text-decoration: none;
    color: #4070f4;
}
.game-holder{
    width: 100%;
    height: 100%;
    border-radius: inherit;
    position: relative;
    transition: .5s;
}

.play-details{
    width: 100%;
    height: auto;
    position: absolute;
    bottom: 0px;
    border-radius: 0px 0px 10px 10px;
    padding: 10px 15px 25px 15px ;
}
.more-options .btn-dark{
    position: relative;
    background-color: #222;
}
.game-holder1{
    
    background-color: #325872;
}
.image-holder{
    position: absolute;
    /* background-color: orange; */
    width: 100%;
    height:100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 90%;
}
.Sliding_Puzzle_game{
    transform: rotate(70deg) translate(-25%, -25%);
    background-size: 90%;
    background-position: center;
   
    background-image: url(images/slidingpuzzle.svg);
}
.Simon_game{
    background-image: url(images/capturesimon.svg);
    transform: translate(-20%, -25%);
}
.master-2048{
    background-image: url(images/capture2048.svg);
    transform: translate(20%, -25%);
}
.hangman{
    background-color: papayawhip;
    transform: none;
    background-image: url(images/gibbet-svgrepo-com.svg);
    background-size: 60%;
    background-position: 50% 10px;
}

.Snake_game{
    background-size: 80%;
    transform: none;
    background-position: 50% 20px;
    background-image: url(images/SNAKE2.PNG);
}
.Card_memory{
    background-image: url(images/capturememory.svg);
    transform: none;
    background-position: 100px 20px;
    background-size: 120%;
}
.Water_sort_game{
    background-image: url(images/watersort.svg);
    background-size: 70%;
    background-position: -30% 20px;
}
.image-Sliding_Puzzle_game{
    background-color: #325872;
}
.image-Snake_game{
    background-color: #dfe9f5;
}
.image-Simon_game{
    background-color: #325872;
}
.image-master-2048{
    background-color: #BBADA0;
}
.image-Card_memory{
    background-color: #325872;
    
}
.image-Water_sort_game{
    background-color: #0F84A7;
}
.im-h2{
    /* background-image: url(capturesimon.svg); */
    transform: translate(-20%, -25%);

}
.ih-5{
    background-color: papayawhip;
    transform: none;
    /* background-image: url(gibbet-svgrepo-com.svg); */
    background-size: 60%;
    background-position: 50% 10px;
}
.g-h2{
    background-color: #BBADA0;
}
.game-holder.gh-3{
    background-color: #FFF5E7;

}
.ih-4{
    /* background-image: url(SNAKE2.PNG); */
    background-size: 80%;
    transform: none;
    background-position: 50% 20px;
}
.ih-6{
    transform: none;
    /* background-image: url(capturememory.svg); */
    background-position: 100px 20px;
    background-size: 120%;
}
.btn-dark::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 7.5px;
    bottom: -3px;
    background-color: #000;
    z-index: -1;
    left: 0px;

}
.details-holder{
    display: flex;
    justify-content: space-between;
}
.game-detail span{
    font-family: "Bricolage Grotesque", sans-serif;
    font-weight: bold;
    font-size: 14px;
}
.game-detail h2{
    font-family: "Poppins", sans-serif;
    font-size:25px;
    font-weight: 1000;
}
.details-holder .btn{
    width: auto;
    height: 100%;
    display: flex;
    align-items: end;
}
.game-detail{
    padding-right: 10px;
}
.btn button{
    padding: 7.5px 0px;    
    width: 100px;
    font-weight: bolder;
    font-family: "Bricolage Grotesque", sans-serif;
    outline: none;
    border: none;
    border-radius: 5px;
    background-color: #111;
    color: #fff;
    position: relative;
    display: block;
}
.kd::after{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 7.5px;
    bottom: -3px;
    background-color: #000;
    z-index: -1;
    left: 0px;
}

@media screen and (max-width:860px) {
   .games li{
    /* width: 320px; */
    height: 300px;
   }
}
@media screen and (max-width:700px) {
    /* .games li{
     width: 280px;
     height: 300px;
    } */
    
    .game-detail span{
        font-size: small;
    }
    .game-detail h2{
        font-size: 20px;
    }
 }
 @media screen and (max-width:620px) {
    /* .games li{
     width: 250px;
     height: 300px;
    } */
 }
 @media screen and (max-width: 560px){
    .games li{
        height: 280px;
    }
