        font-size: 20px;
    }
 }
 @media screen and (max-width: 540px){
    /* .games li{
        width: 220px;
        height: 280px;
    } */
    header{
        padding: 7.5px;
    }
    .game-detail h2{
        font-size: 18px;
    }
    .game-detail span{
        font-size: small;
    }
 }
 @media screen and (max-width: 500px){
    /* .games li{
        width: 200px;
        height: 280px;
    } */
 }
 @media screen and (max-width: 480px){
    /* .games li{
        width: 300px;
        height: 300px;
    } */
     .custom-filter .games{
        padding: 0px;
     }
    .game-detail span{
        font-size: 12px;
        line-height: 15px;
        display: inline-block;
    }
 }
 @media screen and (max-width: 400px){
    .games li{
        height: 300px;
        width: 100%;
        max-width: 300px;
    }
    .game-detail h2{
        font-size:20px;
    }
 }
 